@import "main";

nav {
  background-color: #333;
  overflow: hidden;
}

nav a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

nav a:hover {
  background-color: #ddd;
  color: black;
}

nav a.current {
  background-color: #4CAF50;
  color: white;
}


a:link {
  color: #FF0000;  /* Red */
}

a:visited {
  color: #00FF00;  /* Green */
}

a:hover {
  color: #FF00FF;  /* Magenta */
}

a:active {
  color: #0000FF;  /* Blue */
}
